.insights {
  font-family: 'Inter', sans-serif;

  &__headline {
    font-size: 4.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: -3.6px;
    margin: 0;
    padding: 48px 0;
  }

  &__copy {
    font-size: 1.25rem;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    letter-spacing: -0.6px;
    padding: 24px 0;

    & a {
      color: inherit;
      text-decoration: underline;
    }
  }

  &__button {
    background-color: transparent;
    padding: 12px 24px;
    text-transform: capitalize;
    color: $monochromeBlack;
  }

  &__max-width {
    max-width: 1300px;
    margin: auto;
    padding: 0 25px;
  }

  &__grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    align-self: stretch;
  }

  &__card {
    max-width: 320px;
    padding: 24px 0;

    &__label {
      display: flex;
      justify-content: flex-start;
      gap: 10px;
      padding: 24px 0 16px;
      place-items: center;

      &__category {
        background-color: $blue;
        &__text {
          font-size: 12px;
          font-style: normal;
          font-weight: 700;
          line-height: 150%;
          padding: 2px 8px;
          // pointer-events: none;

          & a {
            color: $whiteSmoke;
          }
        }
      }

      &__read {
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 150%;
      }
    }

    &__title {
      font-size: 24px;
      font-style: normal;
      font-weight: 700;
      line-height: 140%;
      letter-spacing: -0.4.5rem;
    }

    &__copy {
      color: $black;
      font-size: 1rem;
      font-style: normal;
      font-weight: 700;
      line-height: 150%;
    }

    &__more {
      padding-top: 24px;

      & img {
        max-width: 20px;
      }

      & a {
        color: $black;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 150%;
      }
    }
  }
}

.paginate {
  display: flex;
  justify-content: center;
  gap: 20px;
  color: $black;
  place-items: center;

  & .prev,
  .next {
    display: none;
  }
}

.page-numbers {
  color: $black;
}

.current {
  border-radius: 6.25rem;
  background: $taupe;
  box-shadow: 0px 0px 1px 0px $monochromeBlack;
  width: 36px;
  height: 36px;
  padding: 8px;
  text-align: center;
}
