.nav {
  background-color: $whiteSmoke;

  &__menu-search {
    display: flex;
  }

  &__search {
    background-color: $black;
    color: $white;
    display: block;
    @include bp(phone) {
      display: none;
    }
  }

  &__menu {
    background-color: $white;
    color: $black;
    display: none;
    @include bp(phone) {
      display: block;
    }
  }

  &__bar {
    display: flex;
    max-width: 1440px;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
    margin: auto;
    width: 100%;
    min-height: 78px;
    padding: 0 128px;
    @include bp(phone) {
      padding: 0 35px;
    }
  }

  &__links {
    display: flex;
    align-items: flex-start;
    gap: 48px;
    align-self: stretch;
    place-items: center;

    @include bp(phone) {
      display: none;
    }

    &__list {
      &:hover {
        .nav__links__list__sub {
          opacity: 1;
          visibility: visible;
          transition: visibility 0s ease 0s, opacity 0.5s ease 0s;
        }
      }

      &__sub {
        position: absolute;
        width: 100%;
        min-width: 14rem;
        top: 34px;
        left: 0;
        opacity: 0;
        visibility: hidden;
        -webkit-transition: visibility 0s ease 0.6s, opacity 0.5s ease 0s;
        transition: visibility 0s ease 0.6s, opacity 0.5s ease 0s;
        padding: 20px;
        background-color: $white;
        border-radius: 4px;
        -webkit-box-shadow: 0 102px 98px rgba(0, 0, 0, 0.15);
        box-shadow: 0 102px 98px rgba(0, 0, 0, 0.15);
        z-index: 2;

        &--list {
          padding: 6px 0;

          .nav-links {
            display: flex;
            color: $primary;
          }
        }

        &--mobile {
          width: 100%;
          position: relative;
          margin: 0;
          padding: 0;
          overflow: hidden;
          -webkit-box-shadow: inset 0 -7px 12px rgba(0, 0, 0, 0.35) !important;
          box-shadow: inset 0 -7px 12px rgba(0, 0, 0, 0.35) !important;
          border-top: 1px solid $white;

          &--list {
          }
        }
      }
    }
  }

  &__links__wrapper {
    padding-left: 30px;
  }
  &__dropdown {
    display: none;
    position: absolute;
    background-color: $primary;
    text-align: center;
    padding: 40px 42px 0;
    height: 100vh;
    width: 100%;
    grid-auto-rows: minmax(min-content, 0);

    .nav__links__list {
      display: flex;
      justify-content: center;
      flex-direction: column;

      &:last-of-type {
        .nav__items {
          &:last-of-type {
            border-bottom: none;
          }
        }
      }
    }

    .nav__items {
      padding: 20px 0;
    }
  }

  &__items {
    color: $monochromeBlack;
    font-family: 'Inter', sans-serif;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    text-transform: uppercase;
    position: relative;

    @include bp(custom_laptop) {
      font-size: 1.125em;
      margin-right: 0;
    }
    @include bp(phone) {
      border-bottom: 1px solid $white;
      font-size: 1.125em;
      margin-right: 0;
    }
  }

  // & a:last-of-type {
  //   border: none;
  // }

  &__items-dropdown {
    font-family: 'DJ5 Escrow Light Display', sans-serif;
  }

  // &__items:hover {
  //   color: $secondary;
  // }
}

.btn--header {
  position: absolute;
  right: 150px;
  top: 12px;
  margin: 10px;
  background-color: $secondary !important;
  .btn--primary__link {
    color: $black;
  }

  &:hover {
    background-color: $white !important;
    .btn--primary__link {
      color: $black !important;
    }
  }

  @include bp(tablet_small) {
    display: none !important;
  }
}

.btn--header--dropdown {
  display: none;

  @include bp(tablet_small) {
    display: block;
    width: 170px;
    margin: auto;
    padding-top: 10px;
    background-color: $secondary !important;

    .btn--primary__link {
      color: $black !important;
    }
  }
}

.brand__container {
  position: absolute;
  left: 5rem;
  top: 10px;
  overflow: hidden;
  z-index: 2;
  display: flex;
  // width: 100%;
  transition: all 0.2s ease-out;

  @include bp(phone) {
    left: 0;
    right: 0;
    margin: auto;
    width: 200px;
  }

  &__fig {
    width: 270px;
    min-height: 70px;
    margin: 2px 0;
    background-color: $primary;
    padding: 10px;
    transition: all 0.2s ease-in-out;
    // box-shadow: 0px 6px 0px 0px rgba(0, 0, 0, 0.75);

    @include bp(phone) {
      padding: 10px;
    }

    &--a {
      display: flex;
      width: 100%;
      height: 100%;
    }
  }

  &--fixed {
    min-height: 60px;

    .brand__container__fig {
      min-height: 60px;
      padding: 0;
      box-shadow: none;
      margin: 0;
    }
  }
}

.brand__logo {
  max-width: 35px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 5px 0;

  &--no-scroll {
    display: block;
    animation-name: fade-out;
    animation-timing-function: ease-in-out;
    animation-duration: 0.6s;
    // animation-direction: alter nate;
  }
  &--scroll {
    display: none;
    padding: 5px 0;
    max-width: 177px;
    animation-name: fade-out;
    animation-timing-function: ease-in-out;
    animation-duration: 0.5s;
    // animation-direction: alternate;
  }
}

.fixed-position {
  position: fixed;
}

@keyframes fade {
  0% {
    opacity: 1;
  }

  25% {
    opacity: 1;
  }

  75% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fade-out {
  0% {
    opacity: 0;
  }

  25% {
    opacity: 0;
  }

  75% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

.past-event {
  @include bp(from_custom_tablet) {
    display: none !important;
  }
}
