.team {
  font-family: 'Inter', sans-serif;

  &__search {
    display: flex;
    justify-content: center;
    gap: 10px;
  }

  &__dropdown {
    display: inline-block;
    height: 35px;
    line-height: 35px;
    width: 100%;
    background: #fff;
    border: 1px solid #000;
    color: #000;
    padding: 0 5px;
    position: relative;
    z-index: 10;
    min-width: 200px;
    text-align: left;
    cursor: pointer;
    vertical-align: middle;
    margin-bottom: 10px;

    &__title {
      position: relative;
      padding-right: 20px;
      padding-left: 10px;
      display: block;
      max-height: 35px;
      overflow: hidden;
    }

    &__expertise-names {
      position: absolute;
      visibility: hidden;
      left: -1px;
      background: #fff;
      padding: 0px;
      min-width: 100%;
      text-align: left;
      overflow: hidden;
      z-index: 999999;
      margin: 0px !important;
      opacity: 0;
      transition: all 0.2s ease-in-out;
      border: 1px solid #000;
    }

    &__title:hover + &__expertise-names {
      opacity: 1;
      visibility: visible;
    }

    &__expertise-names:hover {
      opacity: 1;
      visibility: visible;
    }
  }

  &__headline {
    font-size: 4.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: -3.6px;
    margin: 0;
    padding: 48px 0;
  }

  &__copy {
    font-size: 1.25rem;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    letter-spacing: -0.6px;

    & a {
      color: inherit;
      text-decoration: underline;
    }
  }

  &__button {
    background-color: transparent;
    padding: 12px 24px;
    text-transform: capitalize;
    color: $monochromeBlack;
  }

  &__max-width {
    max-width: 1300px;
    margin: auto;
    padding: 0 25px;
  }

  &__grid {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: flex-start;
    align-self: stretch;
  }

  &__card {
    width: 22%;
    padding: 30px 0;

    &__image {
      margin: 0;
      width: 200px;
      height: 200px;
    }

    // &__image:after {
    //   content: '\A';
    //   position: absolute;
    //   width: 100%;
    //   height: 100%;
    //   top: 0;
    //   left: 0;
    //   background-color: rgba(233, 215, 112, 0.2);
    //   border-radius: 5px;
    //   opacity: 0;
    //   cursor: pointer;
    //   transition: all 0.5s;
    //   -webkit-transition: all 0.5s;
    // }

    // &__image:hover:after {
    //   opacity: 1;
    // }

    &__name {
      padding: 24px 0 8px;
    }

    &__title {
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      text-transform: uppercase;
    }
  }
}
.dropdown {
  min-width: 310px;
  position: relative;
}

.dropdown * {
  box-sizing: border-box;
}

.select {
  background: $whiteSmoke;
  color: $black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px $taupe solid;
  padding: 16px 32px;
  cursor: pointer;
  transition: background 0.3s;
  font-size: 12px;
  font-weight: 400;
  line-height: 150%; /* 18px */
  text-transform: uppercase;
}

